﻿@font-face { 
  font-family: 'museo-sans-light'; 
  src: url("@{xui-font-path}museosans_100-webfont.eot"); 
  src: url("@{xui-font-path}museosans_100-webfont.eot?#iefix") 
    format("embedded-opentype"), 
    url("@{xui-font-path}museosans_100-webfont.woff") 
    format("woff"), 
    url("@{xui-font-path}museosans_100-webfont.ttf") 
    format("truetype"), 
    url("@{xui-font-path}museosans_100-webfont.svg#MuseoSans100") 
    format("svg"); 
  font-weight: 100; 
  font-style: normal; 
}
